import { FormularioPreguntaModel } from './formulario-pregunta.model';

export class FormularioRespuestaModel {

  public id: number = 0;
  public descripcion: string = '';
  public isText: boolean = null;
  public activo: boolean = null;
  public orden: number = 0;
  public pregunta: FormularioPreguntaModel = null;

  constructor(fields?: {
    id?: number,
    descripcion?: string,
    isText?: boolean,
    activo?: boolean,
    orden?: number,
    pregunta?: FormularioPreguntaModel,

  }) {
    if (fields) { Object.assign(this, fields); }
  }
}
