import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';
import { UtilAlert } from '../../../../commons/util/util.alert';
import { FormularioService } from '../../../../commons/services/aerc-services/formulario.service';
import { FormularioPreguntaModel } from '../../../../commons/models/aerc-models/formulario-pregunta.model';
import { FormularioRespuestaModel } from '../../../../commons/models/aerc-models/formulario-respuesta.model';
import { TipoSolicitudModel } from '../../../../commons/models/aerc-models/tipo-solicitud-model';
import { TipoSolicitudService } from '../../../../commons/services/aerc-services/tipo-solicitud.service';
import Swal from 'sweetalert2';

import { CdkDragDrop, moveItemInArray } from '@angular/cdk/drag-drop';
import { Router } from '@angular/router';
import { SubTask } from 'src/app/commons/interfaces/aerc-interfaces/subtask.interface';
import { Task } from 'src/app/commons/interfaces/aerc-interfaces/task.interface';
import { BlockUI, NgBlockUI } from 'ng-block-ui';
import { Store } from '@ngrx/store';
import { AppState } from 'src/app/store/app.reducer';
import { Subscription } from 'rxjs';
import { PreguntaRespuestaModel } from 'src/app/commons/models/aerc-models/pregunta-respuesta.model';
import { listarPreguntaSolicitud, unsetFormulario } from 'src/app/store/actions/aerc-actions/formulario.actions';
import { guardarCompromiso } from '../../../../store/actions/compromiso.actions';
import { MatDialog } from '@angular/material/dialog';
import { ViewEditFormAercDialogComponent } from 'src/app/commons/components/view-edit-form-aerc-dialog/view-edit-form-aerc-dialog.component';

@Component({
  selector: 'app-admin-formularios',
  templateUrl: './admin-formularios.component.html',
  styleUrls: ['./admin-formularios.style.css']
})
export class AdminFormulariosComponent implements OnInit {

  public formTopBar: FormGroup;
  public listadoTipoSolicitud: TipoSolicitudModel[] = [];
  public isEditPregunta: boolean = false;
  public isEditRespuesta: boolean = false;
  public isVistaPrevia: boolean = false;
  public loading: boolean = false;

  public taskPreview: Task[] = [];

  //*
  public tipoSolicitud: number;
  public listadoPreguntas: FormularioPreguntaModel[] = [];
  public cambioPreguntaRealizado: boolean = false;
  //*

  //*
  public idPregunta: number;
  public listadoRespuestas: FormularioRespuestaModel[] = [];
  public listadoRespuestasPreguntas: FormularioRespuestaModel[] = [];
  public cambioRespuestaRealizado: boolean = false;
  //*

  constructor(
    private alert: UtilAlert,
    private formularioService: FormularioService,
    private tipoSolicitudService: TipoSolicitudService,
    private router: Router,
    private store: Store<AppState>,
    private dialog: MatDialog,

  ) { }

  ngOnInit(): void {
    this.initForm();
    this.cargarTipoSolicitud();
  }

  ngOnDestroy(): void {
    this.store.dispatch(unsetFormulario());
  }

  //* INICIACION
  //* ----------------------------------------------------------------------------------

  initForm() {
    this.formTopBar = new FormGroup({
      tipoSolicitud: new FormControl('')
    });
  }

  volver(): void {
    if (this.cambioPreguntaRealizado || this.cambioRespuestaRealizado) {
      this.confirmarVolver();
    } else {
      this.isEditPregunta = true;
      this.isEditRespuesta = false;
    }
  }

  volverVistaPrevia(): void {
    this.taskPreview = [];
    this.isVistaPrevia = false;
  }

  confirmarVolver() {
    Swal.fire({
      title: '¿Desea Volver?',
      text: "Sus cambios no se guardaran.",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonColor: '#02BC77',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Confirmar',
      cancelButtonText: 'Cancelar',
      reverseButtons: true
    }).then((result) => {
      if (result.isConfirmed) {
        this.cambioPreguntaRealizado = false;
        this.cambioRespuestaRealizado = false;
        this.isEditPregunta = true;
        this.isEditRespuesta = false;
        this.listadoRespuestasPreguntas = [];
      }
    })
  }

  //* CARGAR INFORMACIÓN
  //* ----------------------------------------------------------------------------------

  cargarTipoSolicitud() {
    this.tipoSolicitudService.getAllTipoSolicitudAdmin().subscribe(result => {
      if (result != null) {
        this.listadoTipoSolicitud = result;
      }
    })
  }

  cargarPreguntas(idTipoSolicitud: number) {
    this.tipoSolicitud = idTipoSolicitud;
    this.formularioService.getAllPreguntas(idTipoSolicitud).subscribe(result => {
      if (result.length != 0) {
        this.listadoPreguntas = result;
        this.isEditPregunta = true;
        this.isEditRespuesta = false;
        this.cargarAllRespuestas();
      }
    })
  }

  cargarAllRespuestas() {
    this.formularioService.getAllRespuestas(this.tipoSolicitud).subscribe(result => {
      if (result.length != 0) {
        this.listadoRespuestas = result;
        // console.log(this.listadoRespuestas);

      }
    })
  }

  cargarRespuestas(idPregunta: number) {
    this.idPregunta = idPregunta;
    this.isEditPregunta = false;
    this.isEditRespuesta = true;
    this.listadoRespuestasPreguntas = this.listadoRespuestas.filter(r => r.pregunta.id == idPregunta);
  }

  //* DRAG AND DROP
  //* ----------------------------------------------------------------------------------

  dropPregunta(event: CdkDragDrop<FormularioPreguntaModel[]>) {
    this.cambioPreguntaRealizado = true;
    const previousIndex = this.listadoPreguntas[event.previousIndex].orden;
    const currentIndex = this.listadoPreguntas[event.currentIndex].orden;
    this.listadoPreguntas[event.previousIndex].orden = currentIndex;
    this.listadoPreguntas[event.currentIndex].orden = previousIndex;
    moveItemInArray(this.listadoPreguntas, event.previousIndex, event.currentIndex);
    this.listadoPreguntas.sort((a, b) => a.orden - b.orden);
  }

  // dropRespuesta(event: CdkDragDrop<FormularioRespuestaModel[]>) {
  //   const previousIndex = this.listadoRespuestasPreguntas[event.previousIndex].orden;
  //   const currentIndex = this.listadoRespuestasPreguntas[event.currentIndex].orden;
  //   this.listadoRespuestasPreguntas[event.previousIndex].orden = currentIndex;
  //   this.listadoRespuestasPreguntas[event.currentIndex].orden = previousIndex;
  //   moveItemInArray(this.listadoRespuestasPreguntas, event.previousIndex, event.currentIndex);
  //   this.listadoRespuestasPreguntas.sort((a, b) => a.orden - b.orden);
  // }

  dropRespuesta(event: CdkDragDrop<FormularioRespuestaModel[]>) {
    this.cambioRespuestaRealizado = true;
    let previousIndex = this.listadoRespuestasPreguntas[event.previousIndex];
    let previousIndexOrden = previousIndex.orden;
    let currentIndex = this.listadoRespuestasPreguntas[event.currentIndex];
    let currentIndexOrden = currentIndex.orden;

    this.listadoRespuestas.filter(r => r.id == previousIndex.id)[0].orden = currentIndexOrden;
    this.listadoRespuestas.filter(r => r.id == currentIndex.id)[0].orden = previousIndexOrden;
    moveItemInArray(this.listadoRespuestasPreguntas, event.previousIndex, event.currentIndex);
    this.cargarRespuestas(this.idPregunta);
    this.listadoRespuestasPreguntas.sort((a, b) => a.orden - b.orden);
  }

  //* VISTA PREVIA
  //* ----------------------------------------------------------------------------------

  generarVistaPrevia() {
    if (this.formTopBar.get('tipoSolicitud').value == '') {
      this.alert.toastError("Debe seleccionar un tipo de formulario.");
    } else {
      this.isVistaPrevia = true;
      this.crearFormularioTask();
    }
  }

  crearFormularioTask(): void {
    // console.log("TASK")
    this.listadoPreguntas.forEach(pregunta => {
      let respuestaTask: SubTask[] = [];
      let respuestasTemp = this.listadoRespuestas.filter(r => r.pregunta.id == pregunta.id).sort((a, b) => a.orden - b.orden);
      respuestasTemp.forEach(respuesta => {
        if (respuesta.activo) {
          let respuestaTemp: SubTask = {
            id: respuesta.id,
            name: respuesta.descripcion,
            completed: false,
            color: 'primary',
            isText: respuesta.isText ? true : false,
            valueText: ''
          }
          respuestaTask.push(respuestaTemp);
        }
      });
      if (pregunta.activo) {
        let preguntaTemp: Task = {
          id: pregunta.id,
          number: this.listadoPreguntas.indexOf(pregunta) + 1,
          name: pregunta.descripcion,
          completed: false,
          subtasks: respuestaTask
        }
        this.taskPreview.push(preguntaTemp);
      }
    })
  }

  //* ACCIONES
  //* ----------------------------------------------------------------------------------

  editarPregunta(pregunta: FormularioPreguntaModel) {
    this.openDialogEditFormPregunta(pregunta);
  }

  editarRespuesta(respuesta: FormularioRespuestaModel) {
    this.openDialogEditFormRespuesta(respuesta);
  }

  newItem(isPregunta: boolean) {
    let objeto = null;
    isPregunta ? objeto = new FormularioPreguntaModel() : objeto = new FormularioRespuestaModel();
    if (isPregunta) {
      this.openDialogEditFormPregunta(objeto);
    } else {
      this.openDialogEditFormRespuesta(objeto);
    }
  }

  private openDialogEditFormPregunta(pregunta: FormularioPreguntaModel) {
    const dialogRef = this.dialog.open(ViewEditFormAercDialogComponent, {
      data: { descripcion: pregunta.descripcion }
    });

    dialogRef.afterClosed().subscribe(result => {
      if (result != undefined && result != "") {
        if (pregunta.id == 0) {
          pregunta.descripcion = result.descripcion;
          let lastIndex = this.listadoPreguntas.sort((a, b) => b.orden - a.orden)[0];
          pregunta.orden = lastIndex.orden + 1;
          pregunta.tipoSolicitud = lastIndex.tipoSolicitud;
          pregunta.activo = true;
          this.listadoPreguntas.push(pregunta);
          // console.log(this.listadoPreguntas);
        } else {
          this.listadoPreguntas.filter(p => p.id == pregunta.id)[0].descripcion = result.descripcion;
          // console.log(this.listadoPreguntas);
        }
        this.cambioPreguntaRealizado = true;
      }
    });
  }

  private openDialogEditFormRespuesta(respuesta: FormularioRespuestaModel) {
    const dialogRef = this.dialog.open(ViewEditFormAercDialogComponent, {
      data: { descripcion: respuesta.descripcion }
    });

    dialogRef.afterClosed().subscribe(result => {
      if (result != undefined && result != "") {
        if (respuesta.id == 0) {
          respuesta.descripcion = result.descripcion;
          respuesta.activo = true;
          respuesta.isText = false;
          let lastIndex = this.listadoRespuestasPreguntas.sort((a, b) => b.orden - a.orden)[0];
          if (lastIndex == undefined) {
            respuesta.orden = 1;
            respuesta.pregunta = this.listadoPreguntas.filter(p => p.id == this.idPregunta)[0];
          } else {
            respuesta.orden = lastIndex.orden + 1;
            respuesta.pregunta = lastIndex.pregunta;
          }
          this.listadoRespuestasPreguntas.push(respuesta);
          // console.log(this.listadoRespuestasPreguntas);
        } else {
          this.listadoRespuestasPreguntas.filter(r => r.id == respuesta.id)[0].descripcion = result.descripcion;
        }
        this.cambioRespuestaRealizado = true;
      }
    });
  }

  changeCampoAbierto(respuesta: FormularioRespuestaModel, activar: boolean): void {
    this.cambioRespuestaRealizado = true;
    this.listadoRespuestasPreguntas.filter(r => r.id == respuesta.id)[0].isText = activar;
  }

  changeActivarRespuesta(respuesta: FormularioRespuestaModel, activar: boolean): void {
    this.cambioRespuestaRealizado = true;
    this.listadoRespuestasPreguntas.filter(r => r.id == respuesta.id)[0].activo = activar;
  }

  changeActivarPregunta(pregunta: FormularioPreguntaModel, activar: boolean): void {
    this.cambioPreguntaRealizado = true;
    this.listadoPreguntas.filter(p => p.id == pregunta.id)[0].activo = activar;
  }

  reversarCambios() {
    this.cargarPreguntas(this.tipoSolicitud);
    this.cambioPreguntaRealizado = false;
    this.cambioRespuestaRealizado = false;
  }

  guardarCambios() {

    if (!this.cambioPreguntaRealizado && !this.cambioRespuestaRealizado) {
      this.alert.toastInfo("No tiene cambios nuevos.");
    }
    if (this.cambioPreguntaRealizado) {
      this.formularioService.saveAllPreguntas(this.listadoPreguntas).subscribe(result => {
        if (result) {
          this.alert.infoSwal("Cambios guardados.");
        }
      })
    }

    if (this.cambioRespuestaRealizado) {
      this.formularioService.saveAllRespuestas(this.listadoRespuestasPreguntas).subscribe(result => {
        if (result) {
          this.alert.infoSwal("Cambios guardados.");
        }
      })
    }
  }

}
